<template>
  <div>
    <section class="head">
      <div>
        <a @click="goBack" class="iconfont back"> &#xe614; </a>
        <a class="icon icon_manue"> manue </a>
      </div>
      <h1>{{ productDetail.name }}</h1>
      <div class="chartWrap">
        <span class="share iconfont" @click="shareShow"> &#xe650; </span>
        <span class="bag">
          <em class="font10"> {{ indexData.shoppingCartNum }} </em>
        </span>
        <a
          href="javascript:void(0)"
          @click="$router.push('cart')"
          class="iconfont cart"
        >
          &#xe669;
        </a>
      </div>
    </section>

    <section class="detailImg" :class="{ timerIn: hasTimer }">
      <div class="slideWrap">
        <swiper :options="bannerSwiperOption">
          <swiper-slide
            v-for="(item, index) in productDetail.spuMainImageList"
            :key="index"
          >
            <img class="img" :src="item" alt="" />
          </swiper-slide>
          <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
      </div>
      <span class="icon icon_like"> </span>
      <div class="downTimer" v-if="hasTimer">
        <div>Over 500 minus 200AED</div>
        <div>
          Ends in
          <div class="timer">
            <template v-if="timer.d != '00'">
              <span> {{ timer.d }}</span> :
            </template>
            <span> {{ timer.h }} </span> : <span> {{ timer.m }} </span> :
            <span> {{ timer.s }} </span>
          </div>
        </div>
      </div>
    </section>

    <section class="proContent">
      <div class="blocks top">
        <p class="name">
          {{ productDetail.name }}
        </p>
        <div class="box-flex justify-between">
          <div>
            <p>
              <span class="price" :class="{ haveVip: isVIP }">
                {{ productDetail.priceRange }} AED
              </span>
              <span v-if="isVIP" class="tips_vip"> VIP </span>
              <span class="tips_cutOff">
                {{ productDetail.percentage }} OFF
              </span>
            </p>
            <p>
              <del> {{ productDetail.priceRangeLine }} AED </del>
            </p>
            <p class="sign">
              <span v-if="productDetail.isTypeFour == 1"> Free Shipping </span>
              <span v-if="productDetail.isTypeFour == 1"> | </span>
              <span> Inclusive of(VAT) </span>
            </p>
          </div>
          <!-- 查看评论列表 -->
          <div class="starWrap" @click="commentGo">
            <div class="star">
              <!-- 星星展示 -->
              <span
                class="iconfont"
                :class="{ active: i <= productDetail.commentPoints }"
                v-for="i in 5"
                :key="i"
              >
                <template v-if="i + 0.5 != productDetail.commentPoints">
                  <em> &#xe65f; </em>
                </template>
                <em
                  v-if="i + 0.5 == productDetail.commentPoints"
                  class="icon iconHalfStar"
                >
                </em>
              </span>
            </div>
            <p class="blue mt5">{{ productDetail.commentCount }} Reviews</p>
          </div>
        </div>
      </div>

      <!-- 规格选择 -->
      <div class="blocks sizeChoose mt10" id="anchor">
        <div v-for="(item, index) in productDetail.saleAttr" :key="index">
          <p class="tittle">{{item.name}} <span class="color-res" v-if="false">only 5</span></p>
          <p class="blocks">
            <a
              :class="{ 'active' : i == item.selectIndex,'disable':p.disable}"
              v-for="(p, i) in item
                .productAttributeOptionList"
              :key="i"
              href="javascript:void(0)"
              @click="selectAttribute(index,i,item,p)"
            >
              {{ p.name }}
            </a>
          </p>


        </div>



          <p class="tittle">Number</p>
          <div class="gw_num clearfix">
            <em class="jian iconfont" @click="jian"> &#xe613; </em>
            <input type="number" v-model="num" class="num" />
            <em class="add iconfont" @click="jia"> &#xe715; </em>
          </div>

      </div>
      <div class="blocks shipto mt10">
        <p>
          Ship to {{ productDetail.address }} (
          <span class="blue"> Change Location </span> )
        </p>
        <p>
          Delivered within <span class="black"> Sunday, 11.22.2018 </span> to
          xxxxx
        </p>
      </div>

      <div class="blocks mt10 Description">
        <p class="tittle">Description</p>
        <div class="content" :class="{ open: DescriptionOpen }">
          {{ productDetail.proDetail }}
        </div>
        <p :class="{ toggle: DescriptionOpen, toggleClose: !DescriptionOpen }">
          <span class="blue" @click="DescriptionOpen = !DescriptionOpen">
            {{ !DescriptionOpen ? "See More" : "Show Less" }}
          </span>
        </p>
      </div>

      <div class="blocks mt10 Specifications">
        <p class="tittle">Specifications</p>
        <div class="content" :class="{ open: SpecificationsOpen }">
          <p v-for="(item, index) in productDetail.secondAttr" :key="index">
            <label> {{ item.attrName }} </label>
            <span> {{ item.optName }}</span>
          </p>
        </div>
        <p
          :class="{
            toggle: SpecificationsOpen,
            toggleClose: !SpecificationsOpen
          }"
        >
          <span class="blue" @click="SpecificationsOpen = !SpecificationsOpen">
            {{ !SpecificationsOpen ? "See More" : "Show Less" }}
          </span>
        </p>
      </div>

      <div class="mt10 blocks Reviews">
        <div class="box box-flex justify-between">
          <div class="timerWrap">
            <h3 class="homeTittle">Reviews</h3>
          </div>
          <!-- 查看所有推荐 -->
          <div>
            <a
              href="javascript:void(0)"
              @click="
                $router.push({
                  path: 'reviewsList',
                  query: { spuCode: spuCode }
                })
              "
              class="blue"
            >
              See All</a
            >
          </div>
        </div>
        <div class="content">
          <swiper :options="reviewsSwiperOption">
            <swiper-slide
              v-for="(item, index) in productDetail.commentTen"
              :key="index"
            >
              <div class="user box-flex">
                <span class="userName"> {{ item.userID }} </span>
                <div class="star">
                  <span
                    class="iconfont"
                    :class="{ active: i <= item.starNum }"
                    v-for="i in 5" :key="i"
                  >
                    <template v-if="i + 0.5 != item.starNum">
                      <em> &#xe65f; </em>
                    </template>
                    <em
                      v-if="i + 0.5 == item.starNum"
                      class="icon iconHalfStar"
                    >
                    </em>
                  </span>
                </div>
              </div>
              <div class="says">
                {{ item.says }}
              </div>
            </swiper-slide>
          </swiper>
        </div>
      </div>

      <section class="salesWrap mt10">
        <div class="box box-flex justify-between">
          <div class="timerWrap">
            <h3 class="homeTittle">Best sales of this Category</h3>
          </div>
          <div>
            <a href="" class="blue"> See All</a>
          </div>
        </div>
        <div class="commonPro" style="padding-bottom: 30px;">
          <swiper
            :options="commonSwiperOption"
            ref="bestShareSwiper"
            @click="chooseDetail"
          >
            <swiper-slide
              v-for="(item, index) in productDetail.proListCate2"
              :key="index"
            >
              <div class="item">
                <div class="tips_cutOff" v-if="item.percentage">
                  {{ item.percentage }} OFF
                </div>
                <div class="tips_love iconfont" :class="{ active: item.love }">
                  {{ item.love ? "&#xe653;" : "&#xe652;" }}
                </div>
                <div class="tips_sale" v-if="item.sale"></div>
                <img class="img" :src="item.spuMainImage" alt="" />
              </div>
              <div class="name">{{ item.name }}</div>
              <div class="bottomPrice">
                <p class="red">{{ item.priceRange }} AED</p>
                <p class="gray">{{ item.priceRangeLine }} AED</p>
                <p v-if="item.isTypeFour == 1">Free Shipping</p>
              </div>
              <div class="downShare">
                <div :class="{ active: item.share }">
                  <span class="share iconfont"> &#xe650; </span>
                </div>
                <div :class="{ active: item.cart }">
                  <span class="cart iconfont"> &#xe669; </span>
                </div>
              </div>
            </swiper-slide>
          </swiper>
        </div>
      </section>
    </section>

    <div class="downBtnWrap">
      <van-button type="danger" @click="addCart">Add to Cart</van-button>
      <van-button type="danger" @click="addCartGo"
        >Add and Go to Cart</van-button
      >
    </div>

    <!-- 分享弹窗 -->
    <share-model
      :shareBol="shareBol"
      @shareHide="shareHide"
      @mangban="shareHide"
    ></share-model>
    <!-- 回到顶部 -->
    <div class="go-top" v-if="btnFlag" @click="backTop">
      <img src="../common/images/backTop.png" alt="" />
    </div>
  </div>
</template>

<script>
import shareModel from "./share/shareModel.vue";
import { mapGetters } from "vuex";
import { getProListByName } from "@/api/product";
import { addCart } from "@/api/cart";
import searchVue from './search.vue';
export default {
  name: "proDetail",
  components: { shareModel },
  data() {
    return {
      shareBol: false,
      btnFlag: false,
      skuCode: null,
      num: 1,
      hasTimer: false,
      colorSelect: null,
      sizeSelect: null,
      spuCode: this.$route.query.spuCode,
      bannerSwiperOption: {
        autoplay: true,
        loop: true,
        slidesPerView: 1,
        spaceBetween: 0,
        centeredSlides: true,
        pagination: {
          el: ".swiper-pagination",
          type: "fraction"
        }
      },
      commonSwiperOption: {
        slidesPerView: 2.3,
        spaceBetween: 10,
        freeMode: true,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      },
      reviewsSwiperOption: {
        loop: true,
        slidesPerView: 1.2,
        spaceBetween: 10,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      },
      timer: {
        d: "00",
        h: "00",
        m: "00",
        s: "00"
      },
      starNum: 4.5,
      isVIP: false,
      DescriptionOpen: false,
      SpecificationsOpen: false,
      productDetail:{},
      attributeList:[],
      atrributeMap:{},
      selectIndex:[0,"-",0],
      selectSku:null,
    };
  },
  computed: {
    ...mapGetters([ "user", "indexData"])
  },
  mounted() {
    window.addEventListener("scroll", this.scrollToTop);
    this.init();
    this.isVIP = this.user.level;
  },
  destroyed() {
    window.removeEventListener("scroll", this.scrollToTop);
  },
  methods: {
    init() {
      this.$store
        .dispatch("getProDetailBySpuCode", { spuCode: this.spuCode })
        .then(res => {
          this.productDetail=res;
          let i=0;
          this.productDetail.productSkuList.map(t=>{
              let attrJson=JSON.parse(t.attributeJson);
              attrJson.forEach(e=>{
                for (const key of Object.keys(e)) {
                  this.attributeList.push(key+"-"+e[key]);
                  this.atrributeMap[key+"-"+e[key]]=t;
                }
              });


          });
          // this.countTime();
        })
        .catch(e => {
          console.log(e);
        });
    },
    countTime() {
      //获取当前时间
      var date = new Date();
      var now = date.getTime();
      //设置截止时间
      var endDate = new Date("2019-05-05 23:59:59");
      var end = endDate.getTime();
      //时间差
      var leftTime = end - now;
      //定义变量 d,h,m,s保存倒计时的时间
      if (leftTime >= 0) {
        this.timer.d = this.timeStr(Math.floor(leftTime / 1000 / 60 / 60 / 24));
        this.timer.h = this.timeStr(
          Math.floor((leftTime / 1000 / 60 / 60) % 24)
        );
        this.timer.m = this.timeStr(Math.floor((leftTime / 1000 / 60) % 60));
        this.timer.s = this.timeStr(Math.floor((leftTime / 1000) % 60));
      }

      //递归每秒调用countTime方法，显示动态时间效果
      setTimeout(this.countTime, 1000);
    },
    timeStr(count) {
      if (count < 10) {
        return "0" + count;
      } else {
        return count;
      }
    },
    goBack() {
      this.$router.back(-1);
    },
    chooseDetail(e) {
      let index = this.$refs.bestShareSwiper.swiper.clickedIndex;
      this.goDetail(this.productDetail.proListCate2[index].spuCode);
    },
    goDetail(spuCode) {
      this.$router.push({
        path: "/proDetail",
        query: { spuCode: spuCode }
      });
    },
    //评论列表
    commentGo() {
      this.$router.push({
        path: "/reviewsList",
        query: {
          spuCode: this.spuCode
        }
      });
    },
    jia() {
      this.num++;
    },
    jian() {
      this.num--;
      if (this.num <= 1) {
        this.num = 1;
      }
    },
    selectAttribute(index,itemIndex,attr,item){
      if(item.disable){
          return false;
      }

      this.$set(this.selectIndex,[index==0?0:2],item.id);
      let sIndex= this.selectIndex.join('');

      this.$set(attr,'selectIndex',itemIndex);

      if(this.selectIndex[0]>0&&this.selectIndex[2]>0){
        this.selectSku=this.atrributeMap[sIndex];
        return false;
      }

  
      // 检测数据
      let searchValue=(index==0?item.id:"")+"-"+(index==1?item.id:"");
      let arr=this.attributeList.filter(t=>{return t.indexOf(searchValue)>-1;});
      // 默认全黑
      let otherIndex=index==0?1:0;
      this.productDetail.saleAttr[otherIndex].productAttributeOptionList.forEach(s=>{
          this.$set(s,'disable',true);
      });
      if(arr.length>0){
        arr.forEach(a=>{
          let optionId=a.split("-")[otherIndex];
          let selectResult=this.atrributeMap[a];

          if(selectResult.stock>=0){
              this.productDetail.saleAttr[otherIndex].productAttributeOptionList.forEach(s=>{
                if(s.id==optionId){
                  this.$set(s,'disable',false);
                }
            });
          }
        });

      }

    },
    addCartCom(isToCart) {

      if (this.selectSku == null) {
        this.$toast("Please Select Specification");
        const returnEle = document.querySelector("#anchor");
        if (!!returnEle) {
          returnEle.scrollIntoView(true);
        }
      } else {
        addCart("", this.selectSku.skuCode, this.num).then(res => {
            this.$toast("Add Successfully");
            if (isToCart) {
              this.$router.push("/cart");
            }
        });
      }
    },
    addCart() {
      this.addCartCom();
    },
    addCartGo() {
      this.addCartCom(true);
    },
    //分享弹窗
    shareShow() {
      //alert('1111')
      this.shareBol = true;
    },
    shareHide() {
      this.shareBol = false;
    },
    // 回到顶部
    backTop() {
      let that = this;
      let timer = setInterval(() => {
        let ispeed = Math.floor(-that.scrollTop / 5);
        document.documentElement.scrollTop = document.body.scrollTop =
          that.scrollTop + ispeed;
        if (that.scrollTop === 0) {
          clearInterval(timer);
        }
      }, 16);
    },
    scrollToTop() {
      let that = this;
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      that.scrollTop = scrollTop;
      if (that.scrollTop > 100) {
        that.btnFlag = true;
      } else {
        that.btnFlag = false;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../common/css/detail.scss";
.color-res {
  color: #e33546;
  font-size: 12px;
  margin-left: 10px;
}
.go-top {
  position: fixed;
  right: 5%;
  bottom: 30%;
  z-index: 1001;
  background-color: #e33546;
  width: 30px;
  height: 30px;
  border-radius: 50%;
}
</style>



