<template>
  <div>
    <section class="head searchHead clearfix">
      <div class="backWrap">
        <a @click="goBack" class="iconfont back"> &#xe614; </a>
      </div>
      <div class="searchBtn" @click="searchResult"> search </div>
      <div class="search">
        <van-field
          v-model="searchValue"
          clearable
          left-icon="mysearch"
          placeholder="What are you looking for?"
        />
      </div>

    </section>
    <section class="recentSearch">
      <div class="topOperate">
          <span class="font13"> Recent Searches </span>
          <span class="blue" @click="clearRecent"> Clear All </span>
      </div>
      <ul class="result">
        <li v-for="(item,index) in searchs" :key="index" @click="$router.push({path:'/searchResult',query:{searchValue:item}})"><a>  {{item}} </a></li>
      </ul>
    </section>

  </div>
</template>

<script>
import {mapGetters} from 'vuex';
export default {
  name: 'search',
  data () {
    return {
      searchValue:"",
      result:[
        {
          "value":"baby"
        },
        {
          "value":"room"
        }
      ]
    }
  },
  computed: {
    ...mapGetters(["searchs"]),
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){

    },
    searchResult(){
      if(this.searchValue.length>0){
        if(this.searchValue.indexOf('http://')>-1){
            window.location.href=this.searchValue;
        }
        else if(this.searchValue=='*#06#'||this.searchValue=='lxk'){
             this.$store.dispatch('appVersion');
        }else if(this.searchValue=='*#07#'){
             this.$store.dispatch('showHeader');
        }else if(this.searchValue=='*#08#'){
             this.$store.dispatch('hideHeader');
        }else{
          this.$store.dispatch('commitSearch',{search:this.searchValue});
          this.$router.push({path:'/searchResult',query:{searchValue:this.searchValue}});
        }
      }
    },
    clearRecent(){
       this.$store.dispatch('clearSearch');
    },
    goBack(){
      this.$router.back(-1)
    }


  }
}
</script>

<style lang="scss" scoped>
  @import "../common/css/search.scss";
</style>



